import React from "react";
import { Box, Grid, Typography } from "@material-ui/core";
import ProdutoPai from "./ProdutoPai";

export default function Categoria(props) {
  let grupos = props.produtos;
  grupos = grupos.filter(produto => {
    return produto.categories[0].id === props.id;
  });

  return (
    <Box className="my-5">
      <h5 className="h5-responsive font-weight-bold text-left my-2">
        {props.titulo}
      </h5>
      <Grid container spacing={2}>
        {grupos.map(grupo => (
          <Grid item xs={4} key={grupo.id}>
            <ProdutoPai dados={grupo} tamanho={60} />
            <Typography
              variant="caption"
              display="block"
              align="center"
              gutterBottom
            >
              {grupo.name}
            </Typography>
          </Grid>
        ))}
      </Grid>
    </Box>
  );
}
