import React from "react";
import { useDispatch } from "react-redux";
import { Box, Paper, Button } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import { cartUpdateItemAction } from "../../actions/Cart";
import { green, pink, blue } from "@material-ui/core/colors";
import clsx from "clsx";

export default function ProdutoFilho(props) {
  const { produto } = props;
  const dispatch = useDispatch();

  let genero = produto.attributes.find(obj => obj.name === "Gênero");
  genero = genero ? genero.options[0] : "";

  const useStyles = makeStyles({
    paper: {
      backgroundColor: green[50],
      padding: "5px",
      margin: "5px"
    },
    masculino: {
      backgroundColor: blue[50]
    },
    feminino: {
      backgroundColor: pink[50]
    },
    preco: {
      textAlign: "center"
    },
    buttonComprar: {
      width: "100%",
      color: "white",
      backgroundColor: green[500]
    }
  });
  const classes = useStyles();

  function addItem() {
    dispatch(cartUpdateItemAction(produto, 1));
  }

  return (
    <Paper
      className={clsx(
        classes.paper,
        genero === "Masculino" ? classes.masculino : null,
        genero === "Feminino" ? classes.feminino : null
      )}
    >
      <Box dislay="flex" flexDirection="column" justifyContent="center">
        <Box p={1} className={classes.preco}>
          <div
            dangerouslySetInnerHTML={{
              __html: produto.price_html
            }}
          ></div>
        </Box>
        <Button
          size="small"
          variant="contained"
          onClick={addItem}
          className={classes.buttonComprar}
        >
          Comprar
        </Button>
      </Box>
    </Paper>
  );
}
