import React, { useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import { Container, Slide } from "@material-ui/core";
import Categoria from "./Categoria";
import Trilha from "../../components/Trilha";
import Cart from "../../components/cart";
import { paymentGetData } from "../../actions/Payment";

export default function CompreAqui() {
  const produtos = useSelector(state => state.products.grouped_products);
  const dispatch = useDispatch();

  useEffect(() => {
    paymentGetData(dispatch);
  });

  return (
    <Slide direction="up" in={true} mountOnEnter unmountOnExit>
      <div>
        <Trilha pagename="Compre aqui" />
        <h5>Monte o carnaval do seu jeito</h5>
        <Categoria id={16} titulo={"CAMAROTES"} produtos={produtos} />
        <Categoria id={17} titulo={"BLOCOS"} produtos={produtos} />
        <Container className="fixed-bottom">
          <Cart />
        </Container>
      </div>
    </Slide>
  );
}
