import { CART_UPDATE, CART_ERASE, CART_ERROR } from "./actionTypes";

export function cartUpdateItemAction(produto, quantidade) {
  return { type: CART_UPDATE, produto, quantidade };
}

export function cartRemoveAllItemsAction() {
  //esvazia o carrinho
  return { type: CART_ERASE };
}

export function cartErrorAction(error) {
  return { type: CART_ERROR, error };
}
