import { WOO_API } from "../woocommerce";
import {
  PAYMENT_SHIPPING_METHODS_GET,
  PAYMENT_GATEWAYS_GET,
  PAYMENT_ERROR
} from "./actionTypes";

export function paymentErrorAction(error) {
  return { type: PAYMENT_ERROR, error };
}

export function paymentShippingMethodsGetAction(dados) {
  return { type: PAYMENT_SHIPPING_METHODS_GET, dados };
}

export function paymentGatewaysGetAction(dados) {
  return { type: PAYMENT_GATEWAYS_GET, dados };
}

export function paymentShippingMethodsGet(dispatch) {
  WOO_API.get("shipping/zones/1/methods")
    .then(response => {
      dispatch(paymentShippingMethodsGetAction(response.data));
    })
    .catch(error => {
      dispatch(paymentErrorAction(error.response));
    });
}

export function paymentGatewaysGet(dispatch) {
  WOO_API.get("payment_gateways")
    .then(response => {
      dispatch(paymentGatewaysGetAction(response.data));
    })
    .catch(error => {
      dispatch(paymentErrorAction(error.response));
    });
}

export function paymentGetData(dispatch) {
  paymentShippingMethodsGet(dispatch);
  paymentGatewaysGet(dispatch);
}
