import React, { useState } from "react";
import { useSelector } from "react-redux";
import { IsEmpty, Map } from "react-lodash";
import Produto from "../../components/produto";
import { Drawer, Grid, Box } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import ProdutoFilho from "./ProdutoFilho";

const useStyles = makeStyles(theme => ({
  grid: {
    marginBottom: 50
  },
  media: {
    width: "100%",
    height: "auto"
  },
  drawer: {
    maxHeight: "80vh"
  },
  TituloDrawer: {
    padding: 10
  }
}));

export default function ProdutoPai(props) {
  const TodosProdutos = useSelector(state => state.products.simple_products);
  const produtoPai = props.dados;
  const atributos = produtoPai.attributes;

  let FilhosPorGenero = { masculino: [], feminino: [], outros: [] };
  function getProdutosFilhos(grouped_products_IDs) {
    let genero = false;
    if (grouped_products_IDs) {
      grouped_products_IDs.forEach(id => {
        let produto = TodosProdutos.find(produto => produto.id === id);
        if (produto) {
          genero = produto.attributes.find(obj => obj.name === "Gênero");
          if (genero) {
            if (genero.options[0] === "Masculino") {
              FilhosPorGenero.masculino.push(produto);
            } else {
              FilhosPorGenero.feminino.push(produto);
            }
          } else {
            FilhosPorGenero.outros.push(produto);
          }
        }
      });
    }
  }
  getProdutosFilhos(produtoPai.grouped_products);

  let censura = atributos.find(obj => obj.name === "Censura");
  censura = censura ? censura.options[0] : 0;

  let diferencial = atributos.find(obj => obj.name === "Diferencial");
  diferencial = diferencial ? diferencial.options : [];

  const imgURL = produtoPai.images[0] ? produtoPai.images[0].src : "";
  const imgALT = produtoPai.images[0] ? produtoPai.images[0].alt : "";

  const classes = useStyles();
  const [openDrawer, setOpenDrawer] = useState(false);
  const toggleDrawer = () => event => {
    if (
      event.type === "keydown" &&
      (event.key === "Tab" || event.key === "Shift")
    ) {
      return;
    }

    setOpenDrawer(!openDrawer);
  };

  function listaProdutos(produtos, titulo = "") {
    return (
      <IsEmpty
        value={produtos}
        yes={() => null}
        no={() => (
          <Grid container spacing={1} className={classes.grid}>
            <Grid item xs={12}>
              {titulo}
            </Grid>
            <Map
              collection={produtos}
              iteratee={produto => (
                <Grid item xs={4} key={produto.id}>
                  <ProdutoFilho produto={produto} />
                </Grid>
              )}
            />
          </Grid>
        )}
      />
    );
  }

  return (
    <div onClick={toggleDrawer("bottom")} onKeyDown={toggleDrawer("bottom")}>
      <Produto dados={produtoPai} tamanho={props.tamanho} />
      <Drawer anchor="bottom" open={openDrawer} className={classes.drawer}>
        <Grid container spacing={2} className={classes.TituloDrawer}>
          <Grid item xs={3}>
            <img
              className={classes.media}
              src={imgURL}
              title={imgALT}
              alt={imgALT}
            />
          </Grid>
          <Grid item xs>
            <Box display="flex" flexDirection="column">
              <Box>
                <h4>{produtoPai.name}</h4>
              </Box>
              <Box>{diferencial}</Box>
              <Box>{censura}</Box>
            </Box>
          </Grid>
        </Grid>
        {listaProdutos(FilhosPorGenero.masculino, "Masculino")}
        {listaProdutos(FilhosPorGenero.feminino, "Feminino")}
        {listaProdutos(FilhosPorGenero.outros)}
      </Drawer>
    </div>
  );
}
